export default "<template>\r\n    <div class=\"home\">\r\n        <raw-loader :mytxt=\"require('!raw-loader!./ViduBaiViet2.vue').default\"></raw-loader>\r\n        <h1 class=\"border-bottom\">ViduBaiViet2</h1>\r\n        <b-row>\r\n            <b-col lg=\"3\" v-for=\"(x, k) in ds\" :key=\"k\">\r\n\r\n                <div :class=\"['border', 'p-1', 'mb-1', (k % 2 == 0) ? 'bg-light' : '']\">\r\n                    <h4 class=\"border-bottom\">{{ x.tieude }}</h4>\r\n                    <div class=\"d-flex bd-highlight\">\r\n                        <div class=\"p-2 bd-highlight \">\r\n                            <img :src=\"(getListSrc(x.nd).length != 0) ? getListSrc(x.nd)[0] : '/images2/734032c1-d157-43ba-a4d7-38e9c031c305_shark.jpg'\"\r\n                                height=\"40px\" width=\"60px\" class=\"float-left\" />\r\n                        </div>\r\n                        <div class=\"p-2 flex-grow-1 bd-highlight text-justify\">\r\n                            {{ getText(getStringFromHtml(x.nd), 80) }}\r\n                        </div>\r\n                    </div>\r\n                    <div class=\"text-right\">\r\n                        <button @click=\"onView(x)\">View</button>\r\n                        <button @click=\"onEdit(x)\">Edit</button>\r\n                    </div>\r\n                </div>\r\n            </b-col>\r\n        </b-row>\r\n        <form @submit.stop.prevent=\"onSaveModal\">\r\n            <b-modal ref=\"my-modal1\" size=\"md\" :no-close-on-backdrop=\"true\" hide-footer :title=\"'Bài viết'\">\r\n                <div class=\"d-block\">\r\n                    <div class=\"\">\r\n                        Tieu de : <input v-model=\"sel.tieude\" class=\"mb-1 full-width\" />\r\n                        <CkeditorCom ref=\"ckeditor\" v-model=\"sel.nd\" :height=\"'200px'\"></CkeditorCom>\r\n                    </div>\r\n                    <div class=\"border-top pt-1\">\r\n                        <div class=\"text-right\">\r\n                            <button type=\"button\" @click=\"onSaveModal\" class=\"btn border mr-1\">Save</button>\r\n                            <button type=\"button\" class=\"btn border\" @click=\"hideModal()\">Cancel</button>\r\n                        </div>\r\n                    </div>\r\n                </div>\r\n            </b-modal>\r\n        </form>\r\n\r\n        <b-modal ref=\"mymodalview\" size=\"md\" :no-close-on-backdrop=\"true\" hide-footer :title=\"'Bài viết'\">\r\n            <div class=\"d-block\">\r\n                <!-- {{rec}}\r\n                { \"id\": 1, \"tieude\": \"Bài viết 1\", \"nd\": \"                 -->\r\n                <h4>{{ rec.tieude }}</h4>\r\n                <div class=\"border-bottom mb-1 text-right\">{{ rec.id }}</div>\r\n                <div v-html=\"rec.nd\"></div>\r\n            </div>\r\n        </b-modal>\r\n\r\n\r\n    </div>\r\n</template>\r\n<script>\r\nimport CkeditorCom from \"@/views/Cke/CkeditorCom1.vue\"\r\nimport $ from \"jquery\"\r\nexport default {\r\n    components: {\r\n        CkeditorCom\r\n    },\r\n    data() {\r\n        return {\r\n            ds: [\r\n                {\r\n                    id: 1, tieude: 'Bài viết 1', nd:\r\n                        `\r\n                    <p>Tương tự, &ocirc;ng H&ograve;a, gi&aacute;m đốc một s&agrave;n địa ốc l&agrave; đại l&yacute; chuy&ecirc;n b&aacute;n h&agrave;ng cho chủ đầu tư bất động sản nằm trong top 3 ở TP HCM, cho biết song song với việc cắt giảm 60% nh&acirc;n sự trong qu&yacute; cuối năm ngo&aacute;i, đến qu&yacute; đầu năm 2023, c&ocirc;ng ty bước v&agrave;o giai đoạn cắt giảm mặt bằng, thu hẹp diện t&iacute;ch văn ph&ograve;ng, đ&oacute;ng cửa 2 chi nh&aacute;nh. &quot;T&ocirc;i đ&oacute;ng bớt văn ph&ograve;ng để tiết kiệm chi ph&iacute;, khi n&agrave;o kinh tế khởi sắc mới t&iacute;nh chuyện thu&ecirc; lại&quot;, &ocirc;ng H&ograve;a n&oacute;i.</p>\r\n\r\n<div class=\"fig-picture\" style=\"padding-bottom:0; position:relative\">\r\n<p style=\"text-align:center\"><img alt=\"Thị trường văn phóng cho thuê khu trung tâm quận 1, TP HCM. Ảnh: Quỳnh Trần\" height=\"80\" src=\"https://i1-kinhdoanh.vnecdn.net/2023/04/25/DJI-0727-5682-1682425204.jpg?w=680&amp;h=0&amp;q=100&amp;dpr=1&amp;fit=crop&amp;s=6FbfOAa2w_EyTnbnYlljvQ\" width=\"121\" /></p>\r\n</div>\r\n\r\n<p>Thị trường văn ph&oacute;ng cho thu&ecirc; khu trung t&acirc;m quận 1, TP HCM. Ảnh: <em>Quỳnh Trần</em></p>\r\n\r\n<p>Diễn biến n&agrave;y cũng tương đồng với b&aacute;o c&aacute;o của nhiều c&ocirc;ng ty. Savills Việt Nam cho biết qu&yacute; đầu năm, c&ocirc;ng suất thu&ecirc; văn ph&ograve;ng đang đi xuống. Đơn vị n&agrave;y x&aacute;c nhận nh&oacute;m kh&aacute;ch thu&ecirc; ng&agrave;nh bất động sản như Novaland v&agrave; FLC thu hẹp quy m&ocirc; đ&atilde; dẫn đến lượng ti&ecirc;u thụ văn ph&ograve;ng sụt giảm, bị &acirc;m 9.000 m2 s&agrave;n.</p>\r\n\r\n<p>C&ograve;n theo CBRE Việt Nam, diện t&iacute;ch văn ph&ograve;ng thu&ecirc; mới trong qu&yacute; I rất hạn chế, trong khi đ&oacute; diện t&iacute;ch kh&aacute;ch trả mặt bằng v&agrave; thu hẹp tăng nhiều, dẫn đến diện t&iacute;ch trống của hạng A tăng 3.500 m2 v&agrave; diện t&iacute;ch cho thu&ecirc; mới của hạng B cũng chỉ hơn 2.200 m2 (trong đ&oacute; đ&atilde; bao gồm gần 3.800 m2 của dự &aacute;n mới đi v&agrave;o hoạt động).</p>\r\n                    `\r\n                },\r\n                {\r\n                    id: 2, tieude: 'Bài viết 2', nd:\r\n                        `\r\n                <div>\r\n<h2>What is Lorem Ipsum? <img alt=\"\" height=\"89\" src=\"/images2/734032c1-d157-43ba-a4d7-38e9c031c305_shark.jpg\" style=\"float:right\" width=\"111\" /></h2>\r\n\r\n<p><strong>Lorem Ipsum</strong> is simply dummy text of the printing and typesetting industry. Lorem Ipsum has been the industry&#39;s standard dummy text ever since the 1500s, when an unknown printer took a galley of type and scrambled it to make a type specimen book. It has survived not only five centuries, but also the leap into electronic typesetting, remaining essentially unchanged. It was popularised in the 1960s with the release of Letraset sheets containing Lorem Ipsum passages, and more recently with desktop publishing software like Aldus PageMaker including versions of Lorem Ipsum.</p>\r\n</div>\r\n                `\r\n                },\r\n                { id: 3, tieude: 'Bài viết 3', nd: 'Bài viết 3' },\r\n                { id: 4, tieude: 'Bài viết 4', nd: 'Bài viết 4' },\r\n                { id: 5, tieude: 'Bài viết 5', nd: 'Bài viết 5' },\r\n                { id: 6, tieude: 'Bài viết 6', nd: 'Bài viết 6' },\r\n            ],\r\n            sel: {},\r\n            rec:{}\r\n        }\r\n    },\r\n    methods: {\r\n\r\n        getImgs(html) {\r\n            html = `<div>${html}</div>`;\r\n            var getAllImages = html.getElementsByTagName('img');\r\n            for (var i = 0; i < getAllImages.length; i++) {\r\n                (function (x) {  // closure starts here\r\n                    getAllImages[x].addEventListener('click', function () {\r\n                        alert(this.getAttribute('src'))\r\n                    })\r\n                }(i))\r\n            }\r\n        },\r\n        getStringFromHtml(html) {\r\n            let $text = $(`<div>${html}</div>`);\r\n            let str = $text.text();\r\n            str = str.replace(/\\s+/g, ' ');\r\n            return str;\r\n        },\r\n        getListSrc(html) {\r\n            let kq = [];\r\n            let $text = $(`<div>${html}</div>`);\r\n            let ar = $text.find('img');\r\n            for (var i = 0; i < ar.length; i++) {\r\n                kq.push(ar[i].src);\r\n            }\r\n            return kq;\r\n        },\r\n        getText(text, n) {\r\n            if (text.length > n) {\r\n                text = text.substring(0, n) + '...';\r\n            }\r\n            return text;\r\n        },\r\n        onView(x) {\r\n            //alert(JSON.stringify(this.getListSrc(x.nd)));\r\n            this.rec=JSON.parse(JSON.stringify(x));\r\n            this.$refs.mymodalview.show();\r\n        },\r\n\r\n        showModal() {\r\n            this.$refs[\"my-modal1\"].show();\r\n        },\r\n        hideModal() {\r\n            this.$refs[\"my-modal1\"].hide();\r\n        },\r\n        showModalView() {\r\n            this.$refs.mymodalview.show();//.$refs.my//$refs[\"my-modal1\"].show();\r\n        },\r\n        hideModalView() {\r\n            this.$refs.mymodalview.hide();//.$refs[\"my-modal1\"].hide();\r\n        },\r\n        onSaveModal() {\r\n            let r = JSON.parse(JSON.stringify(this.sel));\r\n            let index = this.ds.findIndex(x => x.id == r.id);\r\n            this.ds[index] = r;\r\n            this.sel = {};\r\n            this.hideModal();\r\n        },\r\n        onEdit(x) {\r\n            this.sel = JSON.parse(JSON.stringify(x));\r\n            this.showModal();\r\n        }\r\n    }\r\n\r\n}\r\n</script>\r\n<style scoped>\r\n.full-width {\r\n    width: 100%;\r\n}\r\n</style>\r\n  ";