<template>
    <div>
        <textarea :id="id" :name="name" :value="value"></textarea>
        <form @submit.stop.prevent="onSaveModal">
            <b-modal ref="my-modal" size="lg" :no-close-on-backdrop="true" hide-footer :title="'List images'">
                <div class="d-block">
                    <div class="">
                        <b-row>
                            <b-col lg="3">
                                <b-tabs content-class="mt-3">
                                    <b-tab title="Images">
                                        <TreeNutCom :items="fonders" @change="fonderChange">
                                        </TreeNutCom>
                                    </b-tab>
                                    <b-tab title="Upload">
                                        <UploadImageCom :path="fonder.path" @upload_done="onUploadDone"></UploadImageCom>
                                    </b-tab>
                                </b-tabs>
                            </b-col>
                            <b-col lg="9">
                                <h4 class="border-bottom">{{ fonder.path }}</h4>
                                <div class="mt-1">
                                    <div v-for="(x, k) in imgs" :key="k"
                                        class="position-relative divwidth float-left mr-1 mb-1">
                                        <a class="border position-absolute" style=" right:0; bottom:0 " href="#"
                                            @click="onDelImg(x)">del</a>
                                        <img :src="x.FullName.replaceAll('\\', '/')" width="60px" height="40px"
                                            :class="{ 'rounded-circle': act == x.Name || actSelect.Name == x.Name }"
                                            @click="actSelect = x" @mouseover="act = x.Name" @mouseleave="act = ''" />
                                    </div>
                                </div>
                            </b-col>
                        </b-row>
                    </div>
                    <div class="border-top pt-1">
                        <div class="text-right">
                            <button type="button" @click="onSaveModal" class="btn border mr-1">Save</button>
                            <button type="button" class="btn border" @click="hideModal()">Cancel</button>
                        </div>
                    </div>
                </div>
            </b-modal>
        </form>
    </div>
</template>

<script>
import fetchClient from '@/fetchClient';
import MyTree from '@/MyTree';
import TreeNutCom from "@/views/Cke/TreeNutCom.vue"
import UploadImageCom from "@/views/Cke/UploadImageCom.vue"

function loadScript(id, url) {
    return new Promise(function (resolve, reject) {
        var script = document.createElement("script");
        script.onload = resolve;
        script.onerror = reject;
        script.src = url;
        script.type = "text/javascript";
        script.setAttribute('id', id);
        document.getElementsByTagName("head")[0].appendChild(script);
    });
};
let inc = new Date().getTime();
export default {
    components: {
        TreeNutCom,
        UploadImageCom
    },
    data() {
        return {
            fonders: [],
            fonder: '',
            imgs: [],
            act: '',
            actSelect: ''
        }
    },
    props: {
        value: {
            type: String
        },
        id: {
            type: String,
            default: () => `editor-${inc}`
        },
        name: {
            type: String,
            default: () => `editor-${++inc}`
        },
        height: {
            type: String,
            default: '90px',
        },
        toolbar: {
            type: Array,
            default: () => [

                { name: 'clipboard', groups: ['clipboard', 'undo'] },
                //{ name: 'editing', groups: ['find', 'selection', 'spellchecker', 'editing'] },
                { name: 'document', groups: ['mode', 'document', 'doctools'] },
                //{ name: 'forms', groups: ['forms'] },
                { name: 'basicstyles', groups: ['basicstyles', 'cleanup'] },
                { name: 'paragraph', groups: ['list', 'indent', 'blocks', 'align', 'bidi', 'paragraph'] },
                //{ name: 'links', groups: ['links'] },
                { name: 'insert', groups: ['insert'] },
                { name: 'styles', groups: ['styles'] },
                //{ name: 'colors', groups: ['colors'] },
                //{ name: 'tools', groups: ['tools'] },
                { name: 'others', groups: ['others'] },
                //{ name: 'about', groups: ['about'] }
            ]
        },
        language: {
            type: String,
            default: 'en'
        },
        extraplugins: {
            type: String,
            default: ''
        }
    },
    beforeUpdate() {
    },
    mounted() {
        let that = this;
        let cke = "/js/ckeditor_4.21.0_full/ckeditor/ckeditor.js";
        let jquery = "/js/ckeditor_4.21.0_full/ckeditor/adapters/jquery.js";
        if (!document.getElementById("myscirpt_full_enhand1")) {
            loadScript("myscirpt_full_enhand1", cke)
                .then(() => {
                    //loadScript("", jquery).then(() => {
                        onReady(that);
                    //})
                })
        } else {
            onReady(that)
        }
        function onReady(that) {
            const ckeditorId = that.id
            const ckeditorConfig = {
                extraPlugins: 'image2,uploadimage',
                removePlugins: 'image,exportpdf',
                toolbarGroups: that.toolbar,
                language: that.language,
                height: that.height,
            }
            var editor = CKEDITOR.replace(ckeditorId, ckeditorConfig)
            /* Add button */
            editor.addCommand("mySimpleCommand", { // create named command
                exec: function (edt) {
                    that.showModal();
                }
            });
            editor.ui.addButton('SuperButton', { // add new button and bind our command
                label: "Click me",
                command: 'mySimpleCommand',
                toolbar: 'insert',
                icon: '/fileupload.png'
            });
            /* Add button */
            
            CKEDITOR.addCss(".cke_editable{cursor:text; font-size: 11px; font-family: tahoma , Arial, sans-serif;}");
            //CKEDITOR.instances[ckeditorId].setData(that.value)
            CKEDITOR.instances[ckeditorId].on('change', () => {
                let ckeditorData = CKEDITOR.instances[ckeditorId].getData()
                if (ckeditorData !== that.value) {
                    that.$emit('input', ckeditorData)
                }
            })
            if (that.value !== CKEDITOR.instances[ckeditorId].getData()) {
                CKEDITOR.instances[ckeditorId].setData(that.value)
            }
            that.getListImgs();
        }
    },

    destroy() {
        /*
      try {
        let editor = window['CKEDITOR'];
        if (editor.instances && editor.instances[this.id]) {
          editor.instances[this.id].destroy();
        }
      } catch (e) {}
      */
    },

    beforeDestroy() {
        const ckeditorId = this.id
        if (CKEDITOR.instances[ckeditorId] && CKEDITOR.instances ) {
            setTimeout(() => {
                let instance = CKEDITOR.instances[ckeditorId];
                instance.destroy();
                //instance = null;
            });
        }
    },
    methods: {
        onUploadDone(data) {
            this.GetFileByDir(this.fonder.path);
        },
        onDelImg(x) {
            this.DelImage(x.FullName);
        },
        onImgClick(path) {
            this.InsertHtml(`<img src=${path} width="50px" height="40px" />`);
            this.hideModal();
        },
        fonderChange(data) {
            this.fonder = data;
            this.GetFileByDir(data.path);
        },
        onSaveModal() {
            if(this.actSelect=='') return;
            let src = this.actSelect.FullName;
            src = src.replaceAll('\\', '/');
            this.InsertHtml(`<img src=${src} width="50px" height="40px" />`);
            this.hideModal();
            this.actSelect='';
        },
        InsertHtml(html) {//good
            CKEDITOR.instances[this.id].insertHtml(html);
        },
        onEmpty() {//good
            CKEDITOR.instances[this.id].setData('');
        },
        onTest() {
            let img = "/images/bao.png";
            this.InsertHtml(`<img src=${img} />`);
        },
        showModal() {
            this.$refs["my-modal"].show();
        },
        hideModal() {
            this.$refs["my-modal"].hide();
        },
        async getListImgs(path) {
            return await fetchClient.get(`/api/Cke/GetFileByDir?dir=${path}`);
        },
        async getListImgs() {
            return await fetchClient.get(`/api/Cke/GetFiles2`).then(res => {
                let ar = res.data;
                let ar2 = [];
                ar.forEach(x => {
                    ar2.push({
                        id: x.Id,
                        name: x.Name,
                        path: x.Path,
                        pid: x.Pid
                    })
                })
                let ar3 = MyTree.getListTree(ar2);
                this.fonders = ar3;
            }).catch(err => {
                console.log(err);
            })
        },
        async GetFileByDir(path) {
            return await fetchClient.get(`/api/Cke/GetFileByDir?dir=${path}`)
                .then(res => {
                    this.imgs = res.data;
                })
        },
        async DelImage(path) {
            return await fetchClient.get(`/api/Cke/DelImage?path=${path}`)
                .then(res => {
                    this.GetFileByDir(this.fonder.path);
                })
        }
    }
}
</script>
<style scoped>
.divwidth {
    width: 60px;
    height: 40px;
}
</style>
