export default {

    getListTree(ds){
        let listRoot=this.listRootId(ds);
        let listTree=[];
        listRoot.forEach(x=>{
            let ds2=this.walkTree(ds,x);
            listTree.push( this.getById(ds2,x) );
        })
        return JSON.parse(JSON.stringify(listTree));// listTree;
    },
    listRootId(ds){
        let kq=[];
        ds.forEach(x=>{
            if(x.pid==0){
                kq.push(x.id)
            }
        })
        return kq;
    },    
    getById(ds,id){
        let kq=-1;
        let index=ds.findIndex(x=>x.id==id);
        if(index>-1){
            kq=ds[index];
        }
        return kq;
    },
    hasChild(ds,id){
        let kq=0;
        let obj=this.getById(ds,id);
        for(var i=0;i<ds.length;i++){
            if(ds[i].id!=id && ds[i].pid==id){
                kq=1;
                break;
            }
        }
        return kq;
    },
    walkTree(ds,startId){
        let s = [];
        let duyet = [];
        let kq = {};
        let root=this.getById(ds,startId);
        s.push(startId);
        duyet.push(startId);
        let count=0;
        while(s.length!=0){
            let layRa=s.pop();
            count++;
            let obj=this.getById(ds,layRa);
            if(this.hasChild(ds,layRa)==1 ){
                obj.children=[];
            }
            ds.forEach(item=>{
                if(item.pid==layRa && duyet.includes(item.id)==false   ){
                    s.push(item.id);
                    duyet.push(item.id);
                    obj.children.push(item);
                }
            })
        }
        return ds;
    }


}
