<template>
    <li>
        <div :class="{ 'bold': isFolder, 'chon': isActiveItem, 'item': true }" @click="onSelect" >
            {{ item.name }}
            <span v-if="isFolder" @click="toggle">[{{ isOpen ? '-' : '+' }}]</span>
        </div>
        <ul v-show="isOpen" v-if="isFolder">
            <TreeNut class="item" v-for="(child, index) in item.children" :key="index" :item="child" 
                :current="current"
                @select="select"
            ></TreeNut>
        </ul>
    </li>
</template>
<script>
export default {
    name:"TreeNut",
    components: {
    },
    props: {
        item: Object,
        current: Object,//Number,
    },
    data: function () {
        return {
            isOpen: true,
        }
    },
    computed: {
        isFolder: function () {
            return this.item.children && this.item.children.length
        },
        isActiveItem: function () {
            if (this.current == null) {
                return false;
            } else {
                return this.current.id == this.item.id;
            }
        }
    },
    mounted(){
    },
    methods: {
        toggle: function () {
            if (this.isFolder) {
                this.isOpen = !this.isOpen
            }
        },
        onSelect: function () {
            this.select(this.item);
        },
        select: function (item) {
            this.$emit('select', item);
        },
    }
}
</script>
<style>
.item {
    cursor: pointer;
}

.bold {
    font-weight: bold;
}

ul {
    padding-left: 2.5em;
    line-height: 1.5em;
    list-style-type: circle;
}

.active {
    background: silver;
    color: #fff;
}
.chon {
    background: black;
    color: #fff;
}
</style>
  